.section-fourth {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
}

.section-top {
    width: 90%;
    margin: 2% 5%;
}

.section-bottom {
    width: 90%;
    margin: 2% 5% 3%;
    display: flex;
    flex-direction: row;
}

.weather {
    text-align: center;
    width: 25%;
}

.weather .title {
    font-size: 28px;
}

.weather img {
    width: 50%;
    height: auto;
    margin: auto;
}

.weather .temperature {
    font-size: 28px;
}

.weather .description {
    font-size: 20px;
}

@media screen and (max-width: 1000px) {

    .weather .title,
    .weather .temperature {
        font-size: 22px;
    }
}