.section-third {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
}

.section-top {
    width: 90%;
    margin: 2% 5%;
}

.details {
    width: 90%;
    margin: 0% 5%;
    display: flex;
    flex-direction: column;
}

.dayDetails {
    display: flex;
    flex-direction: row;
    text-align: center;
    margin: 2% 4%;
}

.dayDetails .details-left {
    display: flex;
    flex-direction: row;
    width: 25%;
    font-size: 18px;
}

.dayDetails .details-center {
    width: 50%;
    font-size: 16px;
}

.details-center .list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.listItem {
    width: 48%;
    display: flex;
    flex-direction: row;
    margin: 0% 1%;
}

.text-left,
.text-right {
    width: 50%;
    border-top: solid 1px grey;
}

.text-left {
    text-align: left;
}

.text-right {
    text-align: right;
}

.dayDetails .details-right {
    width: 25%;
    text-align: center;
    font-size: 18px;
}

.sunrise img,
.sunset img {
    width: 70%;
    height: auto;
}

.details-right img {
    width: 35%;
    height: auto;
}

@media screen and (max-width: 1000px) {
    .details {
        margin: 2% 2%;
        width: 96%;
    }

    .dayDetails {
        flex-wrap: wrap;
    }

    .dayDetails .details-left {
        width: 50%;
    }

    .dayDetails .details-center {
        width: 100%;
        margin-top: 5%;
    }

    .dayDetails .details-right {
        width: 50%;
    }
}