.section-first {
    color: white;
    display: flex;
    flex-wrap: wrap;
    background-color: #8e9eab;
    border-radius: 20px;
}

.section-top-left {
    width: 60%;
    margin: 2% 5%;
}

.section-top-right {
    width: 30%;
    margin: 2% 0%;
    font-size: 28px;
    text-align: center;
}

.section-bottom-left {
    width: 60%;
    margin: 0% 5% 3%;
}

.section-bottom-right {
    width: 30%;
    margin: 0% 0% 3%;
    font-size: 28px;
    text-align: center;
}

.section-top-left .location {
    font-size: 36px;
}

.section-top-left .temperature {
    font-size: 60px;
    letter-spacing: 2px;
}

.section-bottom-left .description {
    font-size: 32px;
}

.section-bottom-left .alert {
    font-size: 28px;
    color: rgba(255, 255, 255, 1);
}

.section-top-right img {
    display: block;
    width: 50%;
    height: auto;
    margin: auto;
}

@media screen and (max-width: 1000px) {
    .section-first {
        flex-direction: column;
    }

    .section-top-left {
        width: 100%;
        margin: 0%;
        text-align: center;
    }

    .section-top-right {
        width: 100%;
    }

    .section-top-right img {
        width: 30%;
        height: auto;
    }

    .section-bottom-left,
    .section-bottom-right {
        width: 100%;
        margin: 0%;
        text-align: center;
    }

    .section-top-left .location {
        font-size: 32px;
    }

    .section-top-left .temperature {
        font-size: 48px;
        letter-spacing: 1px;
    }

    .section-bottom-left .description {
        font-size: 28px;
    }

    .section-right-left .tempMinMax {
        font-size: 28px;
    }
}