body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background: #8e9eab;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to bottom, #eef2f3, #8e9eab);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.notification {
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 1;
}
.container {
    width: 100%;
    height: auto;
    margin: 5% 0%;
    font-family: 'Oswald', sans-serif;
}

.card {
    margin: 0.5% 25%;
    height: auto;
    box-shadow: 0px 0px 16px -3px rgba(0, 0, 0, 0.75);
    -webkit-box-shadow: 0px 0px 16px -3px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 16px -3px rgba(0, 0, 0, 0.75);
    border-radius: 20px;
}

.section-top .title {
    font-size: 28px;
}

@media screen and (max-width: 1000px) {
    .card {
        margin: 2%;
    }

    .section-top .title {
        font-size: 26px;
    }
}
.section-first {
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    background-color: #8e9eab;
    border-radius: 20px;
}

.section-top-left {
    width: 60%;
    margin: 2% 5%;
}

.section-top-right {
    width: 30%;
    margin: 2% 0%;
    font-size: 28px;
    text-align: center;
}

.section-bottom-left {
    width: 60%;
    margin: 0% 5% 3%;
}

.section-bottom-right {
    width: 30%;
    margin: 0% 0% 3%;
    font-size: 28px;
    text-align: center;
}

.section-top-left .location {
    font-size: 36px;
}

.section-top-left .temperature {
    font-size: 60px;
    letter-spacing: 2px;
}

.section-bottom-left .description {
    font-size: 32px;
}

.section-bottom-left .alert {
    font-size: 28px;
    color: rgba(255, 255, 255, 1);
}

.section-top-right img {
    display: block;
    width: 50%;
    height: auto;
    margin: auto;
}

@media screen and (max-width: 1000px) {
    .section-first {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .section-top-left {
        width: 100%;
        margin: 0%;
        text-align: center;
    }

    .section-top-right {
        width: 100%;
    }

    .section-top-right img {
        width: 30%;
        height: auto;
    }

    .section-bottom-left,
    .section-bottom-right {
        width: 100%;
        margin: 0%;
        text-align: center;
    }

    .section-top-left .location {
        font-size: 32px;
    }

    .section-top-left .temperature {
        font-size: 48px;
        letter-spacing: 1px;
    }

    .section-bottom-left .description {
        font-size: 28px;
    }

    .section-right-left .tempMinMax {
        font-size: 28px;
    }
}
.section-second {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.section-top {
    width: 90%;
    margin: 2% 5%;
}

.section-bottom {
    width: 90%;
    margin: 2% 5% 3%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.todayWeather {
    text-align: center;
    width: 25%;
}

.todayWeather .title {
    font-size: 28px;
}

.todayWeather img {
    width: 50%;
    height: auto;
    margin: auto;
}

.todayWeather .temperature {
    font-size: 28px;
}

@media screen and (max-width: 1000px) {
    .section-top {
        text-align: center;
    }

    .todayWeather .title,
    .todayWeather .temperature {
        font-size: 22px;
        text-align: center;
    }
}
.section-third {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.section-top {
    width: 90%;
    margin: 2% 5%;
}

.details {
    width: 90%;
    margin: 0% 5%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.dayDetails {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    text-align: center;
    margin: 2% 4%;
}

.dayDetails .details-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 25%;
    font-size: 18px;
}

.dayDetails .details-center {
    width: 50%;
    font-size: 16px;
}

.details-center .list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.listItem {
    width: 48%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 0% 1%;
}

.text-left,
.text-right {
    width: 50%;
    border-top: solid 1px grey;
}

.text-left {
    text-align: left;
}

.text-right {
    text-align: right;
}

.dayDetails .details-right {
    width: 25%;
    text-align: center;
    font-size: 18px;
}

.sunrise img,
.sunset img {
    width: 70%;
    height: auto;
}

.details-right img {
    width: 35%;
    height: auto;
}

@media screen and (max-width: 1000px) {
    .details {
        margin: 2% 2%;
        width: 96%;
    }

    .dayDetails {
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
    }

    .dayDetails .details-left {
        width: 50%;
    }

    .dayDetails .details-center {
        width: 100%;
        margin-top: 5%;
    }

    .dayDetails .details-right {
        width: 50%;
    }
}
.section-fourth {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.section-top {
    width: 90%;
    margin: 2% 5%;
}

.section-bottom {
    width: 90%;
    margin: 2% 5% 3%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.weather {
    text-align: center;
    width: 25%;
}

.weather .title {
    font-size: 28px;
}

.weather img {
    width: 50%;
    height: auto;
    margin: auto;
}

.weather .temperature {
    font-size: 28px;
}

.weather .description {
    font-size: 20px;
}

@media screen and (max-width: 1000px) {

    .weather .title,
    .weather .temperature {
        font-size: 22px;
    }
}
.section-fifth {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    text-align: center;
}

a {
    text-decoration: none;
    color: grey;
    transition: letter-spacing 0.8s;
}

a:hover {
    color: black;
    letter-spacing: 1px;
}
