.section-second {
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
}

.section-top {
    width: 90%;
    margin: 2% 5%;
}

.section-bottom {
    width: 90%;
    margin: 2% 5% 3%;
    display: flex;
    flex-direction: row;
}

.todayWeather {
    text-align: center;
    width: 25%;
}

.todayWeather .title {
    font-size: 28px;
}

.todayWeather img {
    width: 50%;
    height: auto;
    margin: auto;
}

.todayWeather .temperature {
    font-size: 28px;
}

@media screen and (max-width: 1000px) {
    .section-top {
        text-align: center;
    }

    .todayWeather .title,
    .todayWeather .temperature {
        font-size: 22px;
        text-align: center;
    }
}